  addObjects() {
    this.geometry = new THREE.PlaneBufferGeometry(1, 1, 100, 100);
    this.material = new THREE.ShaderMaterial({
      extensions: {
        derivatives: "extension GL_OES_standard_derivatives : enable",
      },
      fragmentShader,
      vertexShader,
      side: THREE.DoubleSide,
      uniforms: {
        time: { value: this.time },
        resolution: { value: new THREE.Vector4() },
      },
    });

    this.mesh = new THREE.Mesh(this.geometry, this.material);
    this.scene.add(this.mesh);
  }

  render() {
    this.time = this.clock.getDelta();
    this.renderer.render(this.scene, this.camera);
    if (this.mixer) this.mixer.update(this.time);

    /* 
    this is for the custom camera controller I implemented, the problem is that when the 
    camera moves in the z axis it moves on its local z axis and that is a bit problematic
    for our use case here

    this.camControls.update(this.camera, this.keyboard, this.clock);
     */

    window.requestAnimationFrame(this.render.bind(this));
  }

  setupResize() {
    window.addEventListener("resize", this.resize.bind(this));
  }

  resize() {
    console.log("resize works properly");
    this.sizes.width = this.container.offsetWidth;
    this.sizes.height = this.container.offsetHeight;
    this.renderer.setSize(this.sizes.width, this.sizes.height);
    this.camera.aspect = this.sizes.width / this.sizes.height;

    // image cover
    this.imageAspect = 1; //fix this if it creates a problem
    let a1;
    let a2;
    if (this.sizes.height / this.sizes.width > this.imageAspect) {
      a1 = (this.sizes.width / this.sizes.height) * this.imageAspect;
      a2 = 1;
    } else {
      a1 = 1;
      a2 = this.sizes.height / this.sizes.width / this.imageAspect;
    }

    // this.material.uniforms.resolution.value.x = this.sizes.width;
    // this.material.uniforms.resolution.value.y = this.sizes.height;
    // this.material.uniforms.resolution.value.z = a1;
    // this.material.uniforms.resolution.value.w = a2;

    this.camera.updateProjectionMatrix();
  }

  setupKeyboardListner() {
    window.addEventListener("keydown", this.listenKeyboardEvents.bind(this));
  }

  listenKeyboardEvents(event) {
    console.log(this.params.walkingDelta);
    switch (event.code) {
      case "ArrowUp":
      case "KeyW":
        this.controls.moveForward(this.params.walkingDelta);
        break;
      case "ArrowLeft":
      case "KeyA":
        this.controls.moveRight(-this.params.walkingDelta);
        break;
      case "ArrowDown":
      case "KeyS":
        this.controls.moveForward(-this.params.walkingDelta);
        break;
      case "ArrowRight":
      case "KeyD":
        this.controls.moveRight(this.params.walkingDelta);
        break;
    }
  }
